import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import '@dcloudio/vue-cli-plugin-uni/packages/uni-cloud/dist/index.js';

import Vue from 'vue'
import App from './App'



uni.$showLogin = function(title="请先登录！"){
	const name = uni.getStorageSync('name').name
	if (!name) {
		uni.showToast({
			title,
			image:"../../static/加载.png",
			icon:"none"
		})
		setTimeout(() => {
			uni.navigateTo({
				url: '/subpkg/signin/signin',
			})
		}, 500)
		return
	}
}
Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()